import React, { Children, useRef, useEffect } from 'react';
import { Icon } from 'react-materialize'
import cx from 'classnames';
import M from 'materialize-css'

const constants = {
    STYLES: ['large', 'small', 'floating', 'flat'],
    WAVES: ['light', 'red', 'yellow', 'orange', 'purple', 'green', 'teal'],
    SIZES: ['s', 'm', 'l', 'xl'],
    PLACEMENTS: ['left', 'center', 'right'],
    SCALES: ['big', 'small'],
    ICON_SIZES: ['tiny', 'small', 'medium', 'large']
}
let id = 0;

const idgen = () => {
    let oldId = id;
    id += 1;
    return oldId;
}

const CustomButton = ({
    children,
    className,
    node,
    fab,
    modal,
    flat,
    floating,
    large,
    small,
    disabled,
    waves,
    tooltip,
    tooltipOptions = {},
    icon,
    ariaLabel,
    ...props
}) => {
    const _btn = useRef(null);
    const _fab = useRef(null);

    useEffect(() => {
        if (tooltip) {
            const instance = M.Tooltip.init(_btn.current, tooltipOptions);

            return () => {
                instance && instance.destroy();
            };
        }
    }, [tooltip, tooltipOptions, children]);

    useEffect(() => {
        if (fab && _fab.current) {
            const instance = M.FloatingActionButton.init(_fab.current, fab);

            return () => {
                instance && instance.destroy();
            };
        }
    }, [fab]);

    const renderFab = classes => {
        return (
            <div

                ref={_fab}
                className={cx('fixed-action-btn', {
                    toolbar: fab.toolbarEnabled
                })}
            >
                <div className={classes}>
                    <div >{icon || <Icon>edit</Icon>}</div>
                </div>
                <ul aria-label="Left Align">
                    {Children.map(children, child => (
                        <li key={idgen()}>{child}</li>
                    ))}
                </ul>
            </div>
        );
    };

    let C = node;
    let classes = {
        btn: true,
        disabled,
        'waves-effect': waves
    };


    let styles = { flat, floating, large, small };
    constants.STYLES.forEach(style => {
        if (styles[style]) {
            classes.btn = false;
            classes['btn-' + style] = true;
        }
    });



    if (modal) {
        classes['modal-' + modal] = true;
    }

    if (fab) {
        return renderFab(cx(classes, className));
    } else {
        return (
            <C aria-label={ariaLabel}
                {...props}
                disabled={disabled}
                className={cx(classes, className)}
                ref={_btn}
                data-tooltip={tooltip}
            >
                {icon}
                {children}
            </C>
        );
    }
};

CustomButton.defaultProps = {
    node: 'button'
};

export default CustomButton;
